.home-title {
    padding-right: 2em;
    padding-left: 4em;
    text-decoration: none;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: black;
}


.top-line {
    background-color: rgb(192, 220, 241);
    min-height: 48px;
    display: flex;
    /* flex-direction: column; */
    /* justify-content: center; */
    width: 100%;
}
  
.top-line-text {
    margin: auto;
    /* font-family: Arial, Helvetica, sans-serif; */
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 350;
    font-size: 14px;
}

.home-link {
    padding: 30px;
    font-size: x-large;
}

.home-links-container {
    display: flex;
    align-items: center;
}


.search-bar,
.signin {
  margin-left: 200px;
  margin-top: -1.65%;
}

.signin {
    margin-left: 30px;
}

.search-bar {
    padding-left: -50%;
}

.category-bar {
    background-color: black;
    /* min-height: 50px; */
    display: flex;
    width: 100%;
}

.profile-dropdown {
    position: absolute;
}

.category-bar{
    display: flex;
    justify-content: space-between;
}
  
.categories{
    list-style: none;
    padding: 10px;
    margin: 10px;
    margin-right: 10px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.categories li {
    flex: 1;
    text-align: center;
    color: white;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 240;
    font-size: 14px;
    margin-left: 1.2%;
    margin-right: 1%;
}

.heart-icon {
    padding: 0%;
    margin: -1%;
    margin-left: 3%;
    margin-bottom: -2%;
    margin-top: -3%;
}

.community {
    font-family: "helvetica neue",helvetica,arial,sans-serif;
    font-size: 14px;
    margin-left: 30px;
    margin-bottom: -10px;
    margin-top: -30px;
    font-weight: 400;
}


.vl {
    border-left: 0.1px solid rgb(220, 223, 233);
    height: 40px;
    margin-left: 30px;
    margin-bottom: -10px;
    margin-top: -30px;
}

.login-button button {
    font-size: 12px;
    /* padding: 0.25em 0.875em; */
    min-height: 32px;
    font-weight: var(--font-weight-bold);
    border-width: 2px;
    justify-content: center;
    align-items: center;
    line-height: 1;
    border-color: transparent;
    text-align: center;
    text-decoration: none;
    border-radius: 99999px;
    appearance: none;
    color: rgb(255, 255, 255);
    background-color: rgb(0, 0, 0);
    transition: background-color 0.2s ease 0s;
    display: flex;
    width: 130px;
    height: 30px;
    min-width: 5.5em;
    font-family: "helvetica neue",helvetica,arial,sans-serif;
    font-weight: 500;

  }
  
.sign-up button {
    font-size: 12px;
    /* padding: 0.25em 0.875em; */
    min-height: 32px;
    font-weight: var(--font-weight-bold);
    border-width: 2px;
    justify-content: center;
    align-items: center;
    line-height: 1;
    background-color: transparent;
    text-align: center;
    text-decoration: none;
    border-radius: 99999px;
    appearance: none;
    color: rgb(0, 0, 0);
    border-color: black;
    transition: color 0.2s ease 0s, border-color 0.2s ease 0s;
    display: flex;
    width: 130px;
    height: 30px;
    min-width: 5.5em;
    font-family: "helvetica neue",helvetica,arial,sans-serif;
    font-weight: 500;

}

.hidden {
    display: none;
}

.user-dropdown {
    padding-left: 1%;
    margin-top: 1%;
    width: 300px;
    height: 150px;
    background-color: white;
    display: flex;
    flex-direction: row;
    /* justify-content: center; */
    /* align-items: center; */
    box-shadow: rgb(0 0 0 / 20%) 0px 0px 6px;

}

.buttons {
    display: flex;
    /* width: 100%; */
    overflow: hidden;
    white-space: pre-line;
    /* margin-left: 5%; */
    align-items: center;
    padding-top: 5%;
    justify-content: space-between;
}

.drop-message {
   padding: 4%;
   padding-top: 6%;
}

.user-profile-img {
    padding-top: 5px;

}

.button-box {
    background-color: transparent;
}

.profile-signin-message {
    font-family: "helvetica neue",helvetica,arial,sans-serif;
    font-weight: 600;
    font-size: 12px;
}

.free-shipping-signin {
    font-family: "helvetica neue",helvetica,arial,sans-serif;
    font-weight: 400;
    font-size: 11px;
}

.profile-message-container {
    line-height: 12px;
    margin-bottom: -10px;
